.page-footer {
    background: #B90000;
    padding: 20px 0 13px;

    .top-wrapper {
        margin-bottom: 25px;
        align-items: center;
    }

    .top-wrapper,
    .bottom-wrapper {
        display: flex;
        justify-content: space-between;
        color: #EEEEEE
    }

    .logo-wrapper {
        font-size: 0;
        width: 318px;
        height: 86px;
        display: block;
        background: url(../img/logo-footer-tuyengiao.png) center center/318px no-repeat;
    }

    .ios,
    .gg-play {
        font-size: 0;
        width: 148px;
        height: 43px;
        display: block;
        margin: 0 auto;
        margin-top: 10px;
    }

    .text  a {
        color: #fff;
        text-decoration: underline;
    }

    .ios {
        background: url(../img/ios.png) center center no-repeat;

    }

    .gg-play {
        background: url(../img/gg-play.png) center center no-repeat;
    }
}